





















































































































































































@import "../scss/mixins";

.vue-command {
  display: flex;

  .stdin-container {
    display: flex;
  }

  input,
  textarea {
    background: none;
    border: none;
    font-family: "Inconsolata", monospace;
    font-size: 1rem;
    outline: none;
    flex: 1;
    width: 100%;

    &::placeholder {
      color: rgba(255, 255, 255, 0.5);
    }
  }

  .term-stdin {
    flex: 1;
    background: none;
    margin: 0;
    margin-left: 0.5rem;
    border: 0;
    color: inherit;
    font-family: inherit;
    font-size: 1rem;
  }
}
