











































































































































































































































@import "../scss/mixins";

.vue-command {
  ::-webkit-scrollbar {
    width: 5px;
  }

  ::-webkit-scrollbar-thumb {
    background: #ddd;
    -webkit-border-radius: 8px;
    border-radius: 8px;
  }

  .term {
    display: flex;
    flex-direction: column;
    width: 100%;
    border: 1px solid $background;
  }

  .term-bar {
    border-bottom: 1px solid #252525;
    display: flex;
    flex-direction: row;
    height: 32px;
    justify-content: center;
    top: 0;
    width: 100%;
  }

  .term-title {
    font-family: "Montserrat", sans-serif;
    font-size: 0.85rem;
    margin: auto 0;
  }

  .term-std {
    @extend .fullscreen;
  }

  .term-cont {
    font-family: "Inconsolata", monospace;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-bottom: 0.5rem;
    flex: 1;
    margin-top: 10px;
  }

  .fullscreen {
    display: flex;
    flex-direction: column;
    flex: 1;
    height: 100%;
  }
}
